<template>
  <div class="DetailsLunbo">
    <div class="swiper" style="overflow: hidden; text-align: center">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item, index) in arr" :key="index">
          <div class="wrap">
            <div class="close">
              <i @click="$emit('close-Lunbo')"></i>
            </div>
            <div class="imgBox">
              <img :src="item.imageUrl" />
            </div>
            <div class="title">{{ item.title }}</div>
            <div class="content">{{ item.content }}</div>
          </div>
        </div>
        <!-- <div class="swiper-slide">2</div> -->
        <!-- <div class="swiper-slide">3</div> -->
      </div>
    </div>
  </div>
</template>

<script>
import "swiper/swiper-bundle.css";
import swiper from "swiper";
export default {
  props: ["arr", "showLunbo"],
  data() {
    return {
      lunbotu2: null,
    };
  },
  mounted() {
    let vm = this;
    var lunbotu2 = new swiper(".swiper", {
      on: {
        slideChangeTransitionStart() {
          // alert(this.activeIndex);
          // vm.activeIndex = this.activeIndex;
        },
        slideChangeTransitionEnd: function () {},
      },
      initialSlide: 1, //默认第n个
      observer: true, // 修改swiper自己或子元素时，自动初始化swiper
      observeParents: true, // 修改swiper的父元素时，自动初始化swiper
    });

    this.lunbotu = lunbotu2;
    vm;
  },
  watch: {
    showLunbo(n) {
      if (this.showLunbo != "close") {
        // console.log(n);
        this.lunbotu.slideTo(n, 0);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.DetailsLunbo {
  background-color: white;
  z-index: 1;
  margin-top: 50rem;
  width: 375rem;
  height: 617rem;

  .imgBox {
    margin-top: 25rem;
    img {
      width: 300rem;
    }
  }
  .close {
    display: flex;
    justify-content: flex-end;
    height: 32rem;
    align-items: center;
    i {
      margin-top: 10rem;
      display: block;
      width: 16rem;
      height: 16rem;
      margin-right: 25rem;
      background: url("../../assets/交通标志/close.png") center no-repeat;
      background-size: 16rem;
    }
  }
  .title {
    display: block;
    font-size: 18rem;
    color: #333;
    text-align: center;
    padding: 0 27.5rem;
  }
  .content {
    font-size: 15rem;
    color: #787878;
    line-height: 1.7;
    margin-top: 22.5rem;
    padding: 0 27.5rem;
    text-align: left;
  }
}
</style>
