<template>
  <div class="AnswerBottom">
    <div class="shoucang" @click="collect">
      <i
        ><img
          v-if="
            myCollection.findIndex(
              (o) => o == showQuestions[activeIndex].questionId
            ) == -1
          "
          src="../../assets/答题/收藏.png"
        /><img
          src="../../assets/答题/已收藏.png"
          class="hadCollect"
          v-if="
            myCollection.findIndex(
              (o) => o == showQuestions[activeIndex].questionId
            ) !== -1
          "
        /> </i
      >收藏
    </div>
    <div class="yesNo">
      <i class="yes"><img src="../../assets/答题/对题.png" /></i
      ><span class="duishu">{{ dui }}</span>
      <i class="no"><img src="../../assets/答题/错题.png" /></i
      ><span class="cuoshu">{{ cuo }}</span>
    </div>
    <div class="tishu" @click="$emit('appear-Panel')">
      <i><img src="../../assets/答题/题数.png" /></i>
      <span>{{ nowDijiti }}/{{ allQuestionsNumber?.length }}</span>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: ["dui", "cuo", "weidati", "myCollection", "myMistakes"],
  computed: {
    ...mapState([
      "nowDijiti",
      "allQuestionsNumber",
      "activeIndex",
      "showQuestions",
    ]),
    isCollect() {
      return this.myCollection.findIndex(
        (o) => o == this.showQuestions[this.activeIndex].questionId
      );
    },
  },
  methods: {
    collect() {
      this.$emit(
        "collect-timu",
        this.showQuestions[this.activeIndex].questionId
      );
    },
  },
};
</script>

<style lang="less" scoped>
.AnswerBottom {
  position: fixed;
  bottom: 0;
  height: 54rem;
  width: 375rem;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15rem;
  font-size: 13rem;
  i {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 0;
    .hadCollect {
      position: absolute;
      display: flex;
      width: 15rem;
      justify-content: center;
      align-items: center;
    }
  }
  span {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .shoucang {
    color: #999;
    display: flex;
    justify-content: center;
    align-items: center;
    i {
      font-weight: 700;
      // font-size: 25rem;
      width: 20rem;
    }
  }
  .yesNo {
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 15rem;
      margin-right: 5rem;
    }
    .duishu {
      color: #1dacf9;
      margin-right: 15rem;
    }
    .cuoshu {
      color: #ff4a40;
    }
  }

  .tishu {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      color: #999;
    }
    i {
      width: 20rem;
    }
  }
}
</style>
