<template>
  <div class="AnswerPage">
    <TestOver
      :dui="dui"
      :cuo="cuo"
      v-if="!examing && testResult"
      :weidati="weidati"
    ></TestOver>
    <div class="swiper" style="overflow: hidden; text-align: center">
      <div class="swiper-wrapper">
        <div
          class="swiper-slide"
          v-for="(item, index) in showQuestions"
          :key="index"
        >
          <div class="wrap">
            <div class="question">
              <span>{{ item.optionType ? "单" : "判" }}</span>
              {{ item.question }}
            </div>
            <div class="options">
              <OpTions
                @re-cord="record"
                @cho-ose="choose($event)"
                :hadChosen="hadChosen"
                :timu="item"
                :showQuestions="showQuestions"
              ></OpTions>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="BotTombox">
      <BotTom></BotTom>
    </div>
    <div class="ExamBottom" v-if="examing">
      <ul>
        <li class="item" @click="lastQuestion">
          <img src="../../assets/答题/上一题.png" />
          <span>上一题</span>
        </li>
        <li class="item" @click="nextQuestion">
          <img src="../../assets/答题/下一题.png" />
          <span>下一题</span>
        </li>
        <li class="item" @click="appearPanel">
          <img src="../../assets/答题/选择面板.png" />
          <span>{{ nowDijiti }}/{{ 100 }}</span>
        </li>
        <li class="item">
          <img src="../../assets/答题/时钟.png" />
          <van-count-down @finish="finish" :time="time" format="mm:ss" />
        </li>
      </ul>
    </div>
    <AnswerBottom
      :myMistakes="myMistakes"
      :myCollection="myCollection"
      v-show="!examing"
      :dui="dui"
      @collect-timu="collect($event)"
      :weidati="weidati"
      :cuo="cuo"
      @appear-Panel="appearPanel"
    ></AnswerBottom>
    <transition leave-active-class="animate__animated animate__fadeOut">
      <ChoosePanel
        @skip-lunbo="skiplunbo"
        :weidati="weidati"
        :dui="dui"
        :cuo="cuo"
        :recordYesNo="recordYesNo"
        :activeIndex="activeIndex"
        :showPanel="showPanel"
        v-show="showPanel"
        @disapear-Panel="disapearPanel"
      ></ChoosePanel>
    </transition>
  </div>
</template>

<script>
import BotTom from "@/components/BotTom.vue";
import OpTions from "@/components/Exercise/OpTions.vue";
import ChoosePanel from "@/components/Exercise/ChoosePanel.vue";
import TestOver from "@/components/Exercise/TestOver.vue";
import store from "../../store/index.js";
import AnswerBottom from "@/components/Exercise/AnswerBottom.vue";
import { getQuestionsContent } from "../../apis/apis";
import Vuex from "vuex";
const { mapState, mapMutations, mapGetters } = Vuex;
import "swiper/swiper-bundle.css";
import swiper from "swiper";
import { Toast } from "vant";

export default {
  components: { BotTom, OpTions, AnswerBottom, TestOver, ChoosePanel },

  data() {
    return {
      showPanel: false,
      hadChosen: {},
      recordYesNo: {},
      activeIndex: 0,
      lunbotu: null,
      time: 45 * 60 * 1000,
      myMistakes: JSON.parse(localStorage.getItem("myMistakes")) || [],
      myCollection: JSON.parse(localStorage.getItem("myCollection")) || [],
    };
  },
  computed: {
    ...mapState([
      "allQuestionsNumber",
      "nowDijiti",
      "getTenQuestions",
      "showQuestions",
      "loadedQuestion",
      "examing",
      "testResult",
    ]),
    ...mapGetters(["params1"]),
    dui() {
      let d = 0;
      for (let key in this.recordYesNo) {
        if (this.recordYesNo[key] == "blue") {
          d++;
        }
      }
      return d;
    },
    cuo() {
      let d = 0;
      for (let key in this.recordYesNo) {
        if (this.recordYesNo[key] == "red") {
          d++;
        }
      }
      return d;
    },
    weidati() {
      let d = 0;
      for (let key in this.recordYesNo) {
        if (this.recordYesNo[key] !== "") {
          d++;
        }
      }
      return this.allQuestionsNumber?.length - d;
    },
  },
  created() {
    if (this.$route.params.local == "collect") {
      store.dispatch("initialize", {
        allQtNum: this.myCollection,
        skip: false,
      });
    }
    if (this.$route.params.local == "mistakes") {
      store.dispatch("initialize", {
        allQtNum: this.myMistakes,
        skip: false,
      });
    }
  },
  mounted() {
    let vm = this;
    var lunbotu = new swiper(".swiper", {
      on: {
        // slideChangeTransitionStart() {
        //   vm.activeIndex = this.activeIndex;
        // },
        slideChangeTransitionEnd: function () {
          try {
            if (vm.nowDijiti !== 1) {
              //核心功能=======================================
              vm.UpdateNowDijiti(vm.showQuestions[this.activeIndex].xuhao + 1);
              // console.log(vm.loadedQuestion[vm.nowDijiti].xuhao - 1); //现在是loadedQuestion的第几个,按下标,0为第一个
              let first = vm.loadedQuestion[vm.nowDijiti]?.xuhao - 2;
              //==============================================
              if (vm.loadedQuestion[first] && vm.loadedQuestion[first + 3]) {
                vm.updateShowQuestions(
                  vm.loadedQuestion.slice(first, first + 3)
                );
                this.slideTo(1, 0, false);
              } else if (vm.loadedQuestion[first + 4] == 0) {
                getQuestionsContent(
                  vm.allQuestionsNumber.slice(first + 3, first + 13)
                ).then((res) => {
                  console.log(res.data);
                  vm.updateLoadedQuestion({ gotTenQuestions: res.data });
                  vm.updateShowQuestions(
                    vm.loadedQuestion.slice(first, first + 3)
                  );
                  this.slideTo(1, 0, false);
                });
              }
              //核心功能=======================================
            } else {
              vm.UpdateNowDijiti(vm.showQuestions[this.activeIndex]?.xuhao + 1);
            }
            vm.updateActiveIndex(this.activeIndex);
          } catch (error) {
            console.log(error);
          }
        },
      },
      initialSlide: 1, //默认第n个
      observer: true, // 修改swiper自己或子元素时，自动初始化swiper
      observeParents: true, // 修改swiper的父元素时，自动初始化swiper
    });
    lunbotu.slideTo(0, 0, false);
    this.time = 45 * 60 * 1000;
    // this.time = 10 * 1000;
    this.lunbotu = lunbotu;
    this.UpdateNowDijiti(1);
    this.updateActiveIndex(0);
  },
  methods: {
    collect(n) {
      this.myCollection.push(n);
    },
    nextQuestion() {
      this.lunbotu.slideTo(this.nowDijiti, 500);
    },
    lastQuestion() {
      let x = this.nowDijiti - 2;
      this.lunbotu.slideTo(x, 500);
    },
    skiplunbo(n) {
      this.lunbotu.slideTo(n, 0);
    },
    record(n) {
      this.recordYesNo = { ...this.recordYesNo, ...n };
      for (const key in n) {
        console.log(key, n[key]);
        if (
          n[key] == "red" &&
          this.myMistakes.findIndex((o) => o == key) == -1
        ) {
          this.myMistakes.push(Number(key));
        } else if (n[key] == "blue") {
          console.log(this.myMistakes.filter((o) => o !== key));
          this.myMistakes = this.myMistakes.filter((o) => o == key);
        }
      }
    },
    ...mapMutations([
      "UpdateNowDijiti",
      "updateActiveIndex",
      "updateGetTenQuestions",
      "updateShowQuestions",
      "updateAllQuestionsNumber",
      "updateLoadedQuestion",
      "isExaming",
      "showResult",
    ]),
    choose(n) {
      this.hadChosen = { ...this.hadChosen, ...n };
    },
    disapearPanel() {
      this.showPanel = false;
    },
    appearPanel() {
      this.showPanel = true;
    },
    finish() {
      Toast("考试结束");
      this.isExaming(false);
      this.showResult(true);
      
    },
  },
  beforeDestroy() {
    this.updateActiveIndex(this.lunbotu.activeIndex);
    this.showResult(false);
    this.isExaming(false);
  },
  watch: {
    myMistakes() {
      this.myMistakes == new Set(this.myMistakes);
      console.log(this.myMistakes);
      localStorage.setItem("myMistakes", JSON.stringify(this.myMistakes));
    },
    myCollection() {
      this.myCollection == new Set(this.myCollection);
      localStorage.setItem("myCollection", JSON.stringify(this.myCollection));
    },
    
  },
};
</script>

<style lang="less" scoped>
.AnswerPage {
  margin-top: 50rem;
  .swiper {
    background-color: #fff;
    height: 490rem;
    // padding: 20rem 0;
    .swiper-wrapper {
      .swiper-slide {
        .wrap {
          padding: 20rem;
          height: 450rem;
          overflow: auto;
          &::-webkit-scrollbar {
            display: none;
          }
          .question {
            font-size: 18rem;
            text-align: left;
            line-height: 1.7;

            span {
              background-color: #18b4ed;
              color: #fff;
              display: inline-block;
              width: 22rem;
              height: 22rem;
              font-size: 14rem;
              text-align: center;
              line-height: 21rem;
              padding-bottom: 1rem;
              margin-right: 1rem;
              margin-bottom: 2rem;
            }
          }
        }
      }
    }
  }
  .ExamBottom {
    position: fixed;
    bottom: 0;
    background-color: white;
    ul {
      width: 375rem;
      height: 54rem;
      display: flex;
      justify-content: center;
      align-items: center;
      li {
        margin-top: 5rem;
        display: flex;
        justify-content: center;
        height: 100%;
        align-items: center;
        flex-direction: column;
        width: 25%;
        img {
          display: block;
          width: 20rem;
          margin-bottom: 5rem;
          span {
            display: block;
          }
        }
      }
    }
  }
}
</style>
