<template>
  <li class="JiaxiaoItem">
    <div class="left">
      <img :src="jiaxiao.logo.small" alt="" />
      <!-- <van-rate v-model="value" readonly /> -->
    </div>
    <div class="right">
      <h3>
        {{ jiaxiao.name }}<img v-if="hao" src="../../assets/找驾校/好.png" alt="" /><img  v-if="da" 
          src="../../assets/找驾校/大.png"
          alt=""
        /><img v-if="kuai"  src="../../assets/找驾校/快.png" alt="" />
        <img  v-if="gao" src="../../assets/找驾校/高.png" alt="" />
      </h3>
      <div class="score">
        <van-rate
          v-model="value"
          allow-half
          size="13"
          void-icon="star"
          color="rgb(255, 153, 0)"
          readonly
          void-color="#eee"
        />
        <span>{{ jiaxiao.score }}分</span>
      </div>
      <div class="price"><span>￥5400</span> <span>3204学员</span></div>
    </div>
  </li>
</template>

<script>
import Vue from "vue";
import { Rate } from "vant";

Vue.use(Rate);
export default {
  props: ["jiaxiao"],
  data() {
    return {
      value: this.jiaxiao.score,
    };
  },
  computed: {
    hao() {
      return this.jiaxiao.labels.some((o) => o.name == "GOOD_DIANPING");
    },
    da() {
      return this.jiaxiao.labels.some((o) => o.name == "LARGE_STUDENT_COUNT");
    },
    kuai() {
      return this.jiaxiao.labels.some((o) => o.name == "FAST_PASS");
    },
    gao() {
      return this.jiaxiao.labels.some((o) => o.name == "PICK_UP");
    },
  },
};
</script>

<style lang="less" scoped>
.JiaxiaoItem {
  background-color: white;
  height: 100rem;
  padding: 21rem 15rem 12rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  .left {
    margin-right: 12rem;
    img {
      width: 85rem;
    }
  }
  .right {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    height: 66rem;
    flex-direction: column;

    h3 {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 12rem;
      img {
        display: block;
        width: 16rem;
        margin-left: 5rem;
      }
    }
    .score {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .van-rate {
        // width: 55rem;
        // height: 13rem;
        color: rgb(255, 153, 0);
      }
      span {
        display: block;
        margin-left: 20rem;
        color: #666;
      }
    }
    .price {
      span {
        color: #666;
        &:nth-child(1) {
          margin-right: 20rem;
        }
      }
    }
  }
}
</style>
