<template>
  <div id="app">
    <transition leave-active-class="animate__animated animate__fadeOut">
      <TestType
        v-show="canChooseTestType"
        @click.native="clsChooseTestType($event.target.className)"
      ></TestType>
    </transition>
    <transition leave-active-class="animate__animated animate__fadeOut">
      <ChooseArea
        @click.native="clsChooseAarea($event.target.className)"
        v-show="canChooseAarea"
      ></ChooseArea>
    </transition>
    <router-view name="HeaderView"></router-view>
  </div>
</template>

<script>
// import "animate.css";
import Vuex from "vuex";
import ChooseArea from "@/views/ChooseArea.vue";
import TestType from "@/views/TestType.vue";
import { getNumList, getQuestionsContent } from "./apis/apis";

const { mapState, mapMutations, mapGetters } = Vuex;

export default {
  components: {
    ChooseArea,
    TestType,
  },
  data() {
    return {
      canSkip: false,
      esMode: ["sequence", "random", "random", "random"],
    };
  },
  computed: {
    ...mapState([
      "showQuestions",
      "canChooseAarea",
      "canChooseTestType",
      "nowCity",
      "jiaolianbang",
      "mode1",
      "allQuestionsNumber",
      "getTenQuestions",
      "loadedQuestion",
      "activeIndex",
    ]),
    ...mapGetters(["params1"]),
  },
  created() {
    this.axios({
      url: "initial.json",
      methods: "get",
    }).then((res) => {
      // console.log(res);
      this.getCityInfo(res.data);
      this.changeCity(res.data.initial[4][1][1].citys[0]);
      this.changePro(res.data.initial[4][1][1]);
      this.saveLastPro();
    });

    this.axios({
      url: "https://apis.netstart.cn/driver/exercise/sequence?carType=car&course=kemu1&cityCode=440100",
      methods: "get",
    }).then((res) => {
      // console.log(res);
      this.updateAllQuestionsNumber(res.data.questionIds);
    });
  },
  methods: {
    ...mapMutations([
      "updateLoadedQuestion",
      "getCityInfo",
      "changePro",
      "changeCity",
      "closeChooseAarea",
      "saveLastPro",
      "updateAllQuestionsNumber",
      "closeChooseTestType",
      "updatePaihangbang",
      "updatejiaolianbang",
      "updateGetTenQuestions",
      "updateShowQuestions",
      "creatLoadedQuestion",
      "UpdateNowDijiti",
    ]),
    clsChooseAarea(n) {
      if (n == "ChooseArea") {
        this.closeChooseAarea();
      }
    },
    clsChooseTestType(n) {
      // console.log(n);
      if (n == "mask") {
        this.closeChooseTestType();
      }
    },
  },
  watch: {
    params1() {
      getNumList(this.mode1, this.params1)
        .then((res) => {
          this.updateAllQuestionsNumber(res.data.questionIds);
          this.creatLoadedQuestion(this.allQuestionsNumber.length);
          getQuestionsContent(this.allQuestionsNumber.slice(0, 10))
            .then((res2) => {
              let data = [...res2.data];
              this.updateLoadedQuestion({
                gotTenQuestions: [...data],
              });
              this.loadedQuestion.slice(0, 3);
              this.updateShowQuestions(this.loadedQuestion.slice(0, 3));
            })
            .catch((res) => {
              res;
            });
        })
        .catch((res) => {
          res;
        });
      this.$nextTick(function () {
        this.UpdateNowDijiti(1);
      });
    },

    nowCity() {
      Promise.all([
        this.axios.get(
          `https://api2.jiakaobaodian.com/api/h5/jiaxiao/list-jiaxiaos.htm?_appName=jiakaobaodian&_platform=wap&latitude&limit=20&longitude&page=1&sortType=10&_=0.2779912064805148`,
          { params: { cityCode: this.nowCity?.code } }
        ),
        this.axios.get("jiaolian.json"),
      ]).then(([res, res2]) => {
        this.updatePaihangbang(res.data.data.itemList);
        this.updatejiaolianbang(res2.data.data.itemList);
      });
    },
    showQuestions() {
      let n = this.allQuestionsNumber.findIndex((o) => {
        return o == this.showQuestions[1]?.questionId;
      });
      this.UpdateNowDijiti(n + 1);
      if (
        this.activeIndex == 0 &&
        this.showQuestions[0]?.questionId == this.allQuestionsNumber[0]
      ) {
        this.UpdateNowDijiti(1);
      }
    },
  },
};
</script>
<style lang="less">
#app {
  width: 375rem;
  overflow: hidden;
  a {
    text-decoration: none;
    color: black;
  }
}
</style>
