<template>
  <div class="TutorialView">
    <transition
      enter-active-class="animate__animated animate__fadeIn animate__faster"
      leave-active-class="animate__animated animate__fadeOut animate__faster"
    >
      <PlayerCpn
        @close-player="closeplayer"
        v-if="showPlayer"
        :showPlayer="showPlayer"
        :videoUrl="videoUrl"
      ></PlayerCpn>
    </transition>

    <div class="main">
      <div class="title">{{ item.title }}</div>
      <!-- <video :src="videoUrl" controls></video> -->
      <div class="imgBox" @click="showPlayer = true">
        <!-- <img :src="coverUrl" /> -->
        <van-image class="van-image" :src="coverUrl" width="345rem">
          <template v-slot:loading>
            <van-loading show-loading="true" />
          </template>
        </van-image>
        <img class="play" src="../../assets/kemuVideo/play.png" />
      </div>
      <div class="summary">{{ item.description }}</div>
    </div>
    <div class="comment"></div>
  </div>
</template>

<script>
import PlayerCpn from "../../components/kemuVideo/PlayerCpn.vue";

export default {
  components: { PlayerCpn },
  data() {
    return {
      item: null,
      showPlayer: false,
    };
  },
  computed: {
    videoUrl() {
      return "https://apis.netstart.cn/driver/" + this.item.videoLow;
    },
    coverUrl() {
      return "https://apis.netstart.cn/driver/" + this.item.image;
    },
  },
  created() {
    this.item = this.$route.params.item;
  },

  methods: {
    closeplayer() {
      this.showPlayer = false;
    },
  },
};
</script>

<style lang="less" scoped>
.TutorialView {
  margin-top: 50rem;
  background-color: white;
  .main {
    padding: 15rem;
    .title {
      margin-left: 1rem;
      font-size: 23rem;
      font-weight: 700;
      &:hover {
        color: antiquewhite;
      }
    }
    .imgBox {
      position: relative;
      z-index: 0;
      margin: 15rem 0;
      .van-image {
        min-height: 230rem;
        background-color: transparent;
      }
      img {
        width: 345.2rem;
        display: block;
        height: 233.3rem;
      }
      .play {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        width: 50rem;
        height: 50rem;
      }
    }

    .summary {
      margin-left: 1rem;
      padding-right: 11rem;
      text-decoration: none;
      color: #999;
      line-height: 1.7;
      font-size: 1rem;
      margin-top: 5rem;
    }
  }
}
</style>
