<template>
  <div class="OpTions">
    <!-- <img :src="picUrl" v-if="timu.mediaContent" /> -->
    <van-image :src="picUrl" v-if="timu.mediaContent" class="van-image">
      <template v-slot:loading>
        <van-loading type="spinner" size="20" />
      </template>
    </van-image>
    <ul>
      <li
        v-for="(zimu, index) in zimus"
        :key="index + zimu"
        v-show="options[index]"
        @click="choose(zimu)"
      >
        <P
          >{{ zimu
          }}<img
            v-show="yourChoice && crtAnswer == zimu"
            src="../../assets/答题/对题.png"
          />
          <img
            v-show="zimu !== crtAnswer && yourChoice == zimu"
            src="../../assets/答题/错题.png"
          /> </P
        ><span>{{ options[index] }}</span>
      </li>
    </ul>
    <div class="jiexi" v-if="yourChoice">
      <p class="title">试题详解：</p>
      <div class="explain" v-html="timu.explain"></div>
    </div>
    <div class="cheyoufenxi" v-if="yourChoice && cheyoufenxis.length">
      <div class="title">车友分析：</div>
      <ul>
        <CheyouFenxi
          v-for="(cheyoufenxi, index) in cheyoufenxis"
          :key="index"
          :cheyoufenxi="cheyoufenxi"
        ></CheyouFenxi>
      </ul>
    </div>
  </div>
</template>

<script>
import Vuex from "vuex";
const { mapMutations } = Vuex;
import CheyouFenxi from "@/components/Exercise/CheyouFenxi.vue";
export default {
  components: { CheyouFenxi },
  props: ["timu", "hadChosen", "showQuestions"],
  data() {
    return {
      cheyoufenxis: [],
      choice: false,
      zimus: ["A", "B", "C", "D", "E", "F", "G", "H", "I"],
      options: [
        this.timu.optionA,
        this.timu.optionB,
        this.timu.optionC,
        this.timu.optionD,
        this.timu.optionE,
        this.timu.optionF,
        this.timu.optionG,
        this.timu.optionH,
      ],
    };
  },
  computed: {
    // ...mapState(["hadChosen"]),
    picUrl() {
      return "https://apis.netstart.cn/driver/" + this.timu.mediaContent;
    },
    crtAnswer() {
      if (this.timu.answer == 128) {
        return "D";
      } else if (this.timu.answer == 32) {
        return "B";
      } else if (this.timu.answer == 64) {
        return "C";
      } else {
        return "A";
      }
    },
    yourChoice() {
      return this.hadChosen[this.timu.questionId];
    },
  },
  created() {
    this.axios
      .get(`https://dianping-v2.kakamobi.com/api/web/shiti/jinghua-list.htm`, {
        params: {
          placeToken: "5bee2e55901b4de5b15b735eba3056fa",
          topic: this.timu.questionId,
        },
      })
      .then((res) => {
        this.cheyoufenxis = res.data.data?.jinghuaList;
      });
  },
  methods: {
    ...mapMutations(["addChosen"]),
    choose(n) {
      // console.log(this.yourChoice);
      if (!this.yourChoice) {
        this.$emit("cho-ose", { [this.timu.questionId]: n });
      }
      if (n == this.crtAnswer) {
        // console.log("你答对了", n);
        this.$emit("re-cord", { [this.timu.questionId]: "blue" });
      } else {
        // console.log("你答错了", n);
        this.$emit("re-cord", { [this.timu.questionId]: "red" });
      }
    },
  },
  watch: {
    showQuestions() {
      this.options = [
        this.timu.optionA,
        this.timu.optionB,
        this.timu.optionC,
        this.timu.optionD,
        this.timu.optionE,
        this.timu.optionF,
        this.timu.optionG,
        this.timu.optionH,
      ];
      this.axios
        .get(
          `https://dianping-v2.kakamobi.com/api/web/shiti/jinghua-list.htm`,
          {
            params: {
              placeToken: "5bee2e55901b4de5b15b735eba3056fa",
              topic: this.timu.questionId,
            },
          }
        )
        .then((res) => {
          this.cheyoufenxis = res.data?.data?.jinghuaList;
        });
    },
  },
};
</script>

<style lang="less" scoped>
.OpTions {
  // margin-bottom: 126rem;
  // height: 390rem;
  // overflow: auto;
  .van-image {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 335rem;

    min-height: 200rem;
    margin-bottom: -10rem;
    margin-top: 10rem;
  }

  ul {
    li {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      margin: 20rem 0 11rem;
      p {
        width: 35rem;
        height: 35rem;
        border: 1rem solid #e9e9e9;
        border-radius: 50%;
        line-height: 35rem;
        color: #999;
        font-size: 16rem;
        font-weight: 700;
        text-align: center;
        z-index: 0;
        position: relative;
        img {
          display: block;
          position: absolute;
          top: 0;
        }
      }
      span {
        display: block;
        padding-left: 10rem;
        font-size: 16rem;
        color: #666;
        line-height: 35rem;
        text-align: left;
        max-width: 280rem;
        overflow: hidden;
      }
    }
  }
  .jiexi {
    margin: 30rem 0;
    .title {
      text-align: left;
      font-size: 16rem;
      color: #333;
      font-weight: 700;
      margin-bottom: 10rem;
    }
    .explain {
      text-align: left;
      font-size: 14rem;
      line-height: 1.7;
      color: #666;
    }
  }
  .cheyoufenxi {
    .title {
      font-size: 16rem;
      text-align: left;
      color: #333;
      font-weight: 700;
      margin-bottom: 10rem;
    }
  }
}
</style>
