<template>
  <ul class="TrafficSigns">
    <TrafficSigns
      @click.native="biaozhi(index)"
      v-for="(item, index) in homes"
      :item="item"
      :key="index"
    ></TrafficSigns>
  </ul>
</template>

<script>
import TrafficSigns from "@/components/Trafficsign/FourPic.vue";
export default {
  components: {
    TrafficSigns,
  },
  data() {
    return {
      biaozhiLink: [
        "list",
        "仪表盘指示灯",
        "新版交警手势",
        "车内功能按键",
        "交通事故图解",
      ],
      homes: [],
      biaozhiLink2: (function () {
        let a = new Array(18).fill("1");
        a[5] = "新版交警手势";
        a[12] = "车内功能按键";
        a[13] = "仪表盘指示灯";
        a[14] = "交通事故图解";

        return a;
      })(),
    };
  },
  created() {
    this.axios({
      method: "get",
      url: "标志首页1.json",
    }).then((res) => {
      this.homes = [...res.data.data.itemList];
    });
  },
  methods: {
    
    biaozhi(n) {
      if (n == 5 || n == 12 || n == 13 || n == 14) {
        this.axios({
          method: "get",
          url: `${this.biaozhiLink2[n]}.json`,
        }).then((res) => {
          console.log(res.data.data.itemList);
          this.$router
            .push({
              name: "SignView",
              params: { arr: res.data.data.itemList },
            })
            .catch((res) => {
              res
            });
        });
      }
    },
  },
};
</script>

<style lang="less" scoped>
.TrafficSigns {
  margin-top: 50rem;
}
</style>
