import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";
import HeaderView from "../views/HeaderView.vue";
import KumuExercise from "../views/Exercise/KumuExercise.vue";
import ZhaoJiaXiao from "../views/homeView/ZhaoJiaXiao.vue";
import ZhaoJiaoLian from "../views/homeView/ZhaoJiaoLian.vue";
import ZhaoPeiLian from "../views/homeView/ZhaoPeiLian.vue";
import JiaoKaoQuan from "../views/homeView/JiaoKaoQuan.vue";
import ChaptersView from "../views/homeView/ChaptersView.vue";
import CommunityView from "../views/homeView/CommunityView.vue";
import TrafficSigns from "../views/homeView/TrafficSigns.vue";
import PostDetail from "../views/homeView/PostDetail.vue";
import SignView from "../views/sign/SignView.vue";
import KemuVideo from "../views/KemuVideo/KemuVideo.vue";
import TutorialView from "../views/KemuVideo/TutorialView.vue";
import AnswerPage from "../views/Exercise/AnswerPage.vue";
import ChapterExercise from "../views/Exercise/ChapterExercise.vue";
import TestRecord from "../components/Exercise/TestRecord.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    components: { HeaderView },
    children: [
      {
        name: "ChaptersView",
        path: "ChaptersView",
        components: { ChaptersView },
      },
      {
        name: "PostDetail",
        path: "PostDetail",
        components: { PostDetail },
      },
      {
        name: "TestRecord",
        path: "TestRecord",
        components: { TestRecord },
      },
      {
        path: "KemuVideo",
        components: { KemuVideo },
      },
      {
        path: "TutorialView",
        name: "TutorialView",
        components: { TutorialView },
      },

      {
        path: "",
        name: "HomeView",
        components: { HomeView },
      },
      {
        path: "ruanjianxiazai",
        components: { HomeView },
      },
      {
        path: "zhaojiaxiao",
        components: { ZhaoJiaXiao: ZhaoJiaXiao },
      },
      {
        path: "TrafficSigns",
        components: { TrafficSigns },
      },
      {
        path: "TrafficSigns/SignView",
        name: "SignView",
        components: { SignView },
      },
      {
        path: "zhaojiaolian",
        components: { ZhaoJiaoLian },
        // component: ZhaoJiaoLian,
      },
      {
        path: "zhaopeilian",
        components: { ZhaoPeiLian },
      },
      {
        path: "KumuExercise",
        components: { KumuExercise },
      },
      {
        path: "answer",
        name: "AnswerPage",
        components: { AnswerPage },
      },
      {
        path: "community",
        name: "CommunityView",
        components: { CommunityView },
      },
      {
        path: "jiakaoquan",
        components: { JiaoKaoQuan },
      },
      {
        path: "zhangjielianxi",
        components: { ChapterExercise },
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});
router.afterEach(() => {
  window.scrollTo(0, 0);
});
export default router;
