<template>
  <div class="TestOver">
    <div class="wrap">
      <div class="circle">
        <van-circle
          v-model="currentRate"
          :rate="dui"
          layer-color="rgb(235, 237, 240)"
          :stroke-width="80"
          size="200rem"
          :text="dui + ' 分'"
        />
      </div>

      <ul class="content">
        <!-- <li>
          <p>分数</p>
          <span>97</span>
        </li> -->
        <li>
          <p><i></i>获得称号</p>
          <span>{{ dui >= 90 ? "木仓车神" : "马路杀手" }}</span>
        </li>

        <li>
          <p><i></i>答对</p>
          <span>{{ dui }}题</span>
        </li>
        <li>
          <p><i></i>答错</p>
          <span>{{ cuo }}题</span>
        </li>
        <li>
          <p><i></i>正确率</p>
          <span
            >{{ dui == 0 ? 0 : ((dui / (dui + cuo)) * 100).toFixed() }}%</span
          >
        </li>
        <li>
          <p><i></i>未答题</p>
          <span>{{ weidati }}题</span>
        </li>
      </ul>
      <div
        class="backHome"
        @click="
          showResult(false);
          $router.push({ path: '/' });
        "
      >
        返回主页
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  props: ["dui", "cuo", "weidati"],
  data() {
    return {
      currentRate: 0,
      testRecord: [],
    };
  },
  created() {
    let obj = {
      score: this.dui,
      testTime: (function () {
        let d = new Date();
        let testTime =
          d.toLocaleDateString().split("/").join("-") +
          " " +
          d.toLocaleTimeString();
        return testTime;
      })(),
      designation: this.dui >= 90 ? "木仓车神" : "马路杀手",
    };
    console.log(obj);
    (this.testRecord = JSON.parse(localStorage.getItem("testRecord")) || []),
      console.log(this.testRecord);
    this.testRecord.push(obj);
  },
  methods: {
    ...mapMutations(["showResult"]),
  },
  watch: {
    dui(new1) {
      this.currentRate = new1;
    },
    testRecord() {
      localStorage.setItem("testRecord", JSON.stringify(this.testRecord));
    },
  },
};
</script>

<style lang="less" scoped>
.TestOver {
  position: fixed;
  top: 0;
  margin-top: 50rem;
  background-color: white;
  height: 617rem;
  width: 375rem;
  z-index: 4;
  padding: 0 40rem;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  .van-circle {
    /deep/.van-circle__text {
      font-size: 50rem;
      color: rgb(25, 137, 250);
    }
  }
  .wrap {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-top: 30rem;
    flex-direction: column;
    .circle {
      // margin-bottom: 50rem;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    ul {
      width: 80%;
      margin: 35rem 0;
      li {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 20rem;
        margin: 15rem 0;
        span {
          display: block;
          color: rgb(25, 137, 250);
        }
        p {
          font-weight: 550;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          i {
            width: 10rem;
            height: 10rem;
            display: block;
            background-color: rgb(27, 103, 255);
            margin-right: 10rem;
            border-radius: 50%;
          }
        }
        &:nth-of-type(2) {
          i {
            background-color: rgb(0, 189, 0);
          }
        }
        &:nth-of-type(3) {
          i {
            background-color: red;
          }
        }
        &:nth-of-type(4) {
          i {
            background-color: rgb(243, 178, 0);
          }
        }
        &:nth-of-type(5) {
          i {
            background-color: #999;
          }
        }
      }
    }
    .backHome {
      width: 250rem;
      font-size: 20rem;
      border: 1rem solid rgb(25, 137, 250);
      height: 50rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 25rem;
      color: rgb(25, 137, 250);
    }
  }
}
</style>
