import Vue from "vue";
import Vuex from "vuex";
import { getQuestionsContent } from "../apis/apis";
import router from "../router/index.js";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    data: [],
    nowPro: {},
    carTypes: ["小车", "货车", "摩托车", "客车"],
    zigeTpyes: ["客运", "货运", "危险品", "教练员", "出租车"],
    nowCity: null,
    canChooseAarea: false,
    canChooseTestType: false,
    clickTimes: 0,
    lastPro: {},
    carType: "car",
    course: "kemu1",
    showInKemu: "小车",
    paihangbang: [],
    jiaolianbang: [],
    showLogo: false,
    allQuestionsNumber: [],
    nowDijiti: 1,
    getTenQuestions: [],
    showQuestions: [],
    mode1: "sequence",
    loadedQuestion: null,
    keumuVideo: 2,
    VideoCartype: "car",
    activeIndex: 0,
    chaptersList: [],
    examing: false,
    testResult: false,
  },
  getters: {
    params1: (state) => {
      return {
        carType: state.carType,
        cityCode: state.nowCity?.code,
        course: state.course,
      };
    },
  }, //计算属性
  mutations: {
    showResult(state, n) {
      state.testResult = n;
    },
    isExaming(state, n) {
      state.examing = n;
    },
    saveChaptersList(state, arr) {
      state.chaptersList = arr;
    },
    randomCoach(state) {
      state.jiaolianbang = state.jiaolianbang.sort(function () {
        return Math.random() - 0.5;
      });
      state.jiaolianbang = state.jiaolianbang.sort(function () {
        return Math.random() - 0.5;
      });
    },
    randomJiaxiaobang(state) {
      // console.log("randomCoach");
      state.paihangbang = state.paihangbang.sort(function () {
        return Math.random() - 0.5;
      });
      state.paihangbang = state.paihangbang.sort(function () {
        return Math.random() - 0.5;
      });
    },
    updateActiveIndex(state, n) {
      state.activeIndex = n;
    },
    updateLoadedQuestion(state, { gotTenQuestions }) {
      let start = state.allQuestionsNumber.findIndex((o) => {
        return o == gotTenQuestions[0].questionId;
      }); //拿到的十个题第一个题在总题目的哪个下标位置

      gotTenQuestions.forEach((ele, i) => {
        ele.xuhao = start + i;
      }); //添加序号属性
      gotTenQuestions.forEach((ele, i) => {
        state.loadedQuestion[start + i] = ele;
      });
    },

    creatLoadedQuestion(state, n) {
      state.loadedQuestion = new Array(n).fill(0);
    },
    changeMode(state, n) {
      state.mode1 = n;
    },
    changeCouse(state, n) {
      state.course = n;
    },
    updateShowQuestions(state, n) {
      state.showQuestions = [...n];
    },
    updateGetTenQuestions(state, n) {
      // n=n.forEach((ele,i) => {
      //   ele.xiabiao
      // });
      state.getTenQuestions = [...n];
    },
    UpdateNowDijiti(state, n) {
      state.nowDijiti = n;
    },
    // addChosen(state,n){
    //   state.hadChosen=[...state.hadChosen,n]
    // },
    updateAllQuestionsNumber(state, n) {
      state.allQuestionsNumber = n;
    },
    hideLogo(state) {
      state.showLogo = false;
    },
    showLogo(state) {
      state.showLogo = true;
    },
    getCityInfo(state, data) {
      state.data = data;
    },
    changePro(state, province) {
      state.nowPro = province;
    },
    changeCity(state, city) {
      state.clickTimes++;
      state.nowCity = city;
    },
    openChooseAarea(state) {
      state.clickTimes = 0;
      state.canChooseAarea = true;
    },
    openChooseTestType(state) {
      state.canChooseTestType = true;
    },
    closeChooseAarea(state) {
      state.canChooseAarea = false;
      if (!state.clickTimes) {
        state.nowPro = { ...state.lastPro };
      } else {
        this.commit("saveLastPro");
      }
    },
    closeChooseTestType(state) {
      state.canChooseTestType = false;
    },
    saveLastPro(state) {
      state.lastPro = { ...state.nowPro };
    },
    changeCarType(state, n) {
      state.carType = n;
      this.commit("closeChooseTestType");
    },
    changeShowInKemu(state, testType) {
      state.showInKemu = testType;
    },
    updatePaihangbang(state, n) {
      state.paihangbang = n;
    },
    updatejiaolianbang(state, n) {
      state.jiaolianbang = n;
    },
  }, //修改state的方法
  actions: {
    initialize({ commit, state }, { allQtNum, upset = false, skip = true }) {
      try {
        if (upset) {
          allQtNum = allQtNum.sort(() => {
            return Math.random() - 0.5;
          });
          allQtNum = allQtNum.sort(() => {
            return Math.random() - 0.5;
          });
        }
        commit("updateAllQuestionsNumber", allQtNum);

        commit("creatLoadedQuestion", state.allQuestionsNumber.length);
        getQuestionsContent(state.allQuestionsNumber.slice(0, 10))
          .then((res2) => {
            let data = [...res2.data];
            state.getTenQuestions = data;
            // console.log(data, "调试");
            commit("updateLoadedQuestion", {
              gotTenQuestions: [...data],
            });
            commit("updateShowQuestions", state.loadedQuestion.slice(0, 3));
            commit("UpdateNowDijiti", 1);
            if (skip) {
              router.push({ name: "AnswerPage" });
            }
          })
          .catch((res) => {
            res;
          });
      } catch (error) {
        console.log(error);
      }
    },
  }, //可以异步
  modules: {},
});
