<template>
  <div class="JiaoKaoQuan">
    <div class="list-title">驾考车友圈</div>
    <div class="cheyouquan">
      <div class="items">
        <div class="swiper" style="overflow: hidden; text-align: center">
          <div class="swiper-wrapper">
            <div class="swiper-slide">
              <a
                @click.prevent="enterCommunity(cheyouquanNum[index])"
                v-for="(item, index) in cheyouquan.slice(0, 4)"
                :key="item + index"
              >
                <img :src="require(`../../assets/homePic/` + item + `.png`)" />
                <span>{{ item }}</span>
              </a>
            </div>
            <div class="swiper-slide">
              <a
                @click.prevent="enterCommunity(cheyouquanNum[index + 4])"
                v-for="(item, index) in cheyouquan.slice(4, 8)"
                :key="item + index"
              >
                <!-- <img :src="require(`../assets/homePic/` + item + `.png`)" /> -->
                <img :src="require(`../../assets/homePic/` + item + `.png`)" />
                <span>{{ item }}</span>
              </a>
            </div>
            <div class="swiper-slide">
              <a
                v-for="(item, index) in cheyouquan.slice(8, 9)"
                :key="item + index"
              >
                <img :src="require(`../../assets/homePic/` + item + `.png`)" />
                <span>{{ item }}</span>
              </a>
            </div>
          </div>
          <!-- <div class="swiper-pagination"></div> -->
        </div>
      </div>
    </div>
    <div class="list-title">热门话题</div>
    <HotTopic
      v-for="(hotTopic, index) in hotTopics"
      :key="index"
      :hotTopic="hotTopic"
    ></HotTopic>
  </div>
</template>

<script>
import "swiper/swiper-bundle.css";
import swiper from "swiper";
import HotTopic from "../../components/cheyouquan/HotTopic.vue";

export default {
  components: { HotTopic },
  data() {
    return {
      cheyouquan: [
        "考前祈愿",
        "科目一",
        "科目二",
        "科目三",
        "科目四",
        "晒驾照",
        "校花校草",
        "学车趣事",
        "学车问答",
      ],
      cheyouquanNum: [351, 347, 348, 349, 350, 345, 341, 353],
      hotTopics: [],
    };
  },
  created() {
    this.axios({
      url: "https://cheyouquan.kakamobi.com/api/web/jiakao/list-by-jinghua.htm",
    }).then((res) => {
      console.log(res.data.data.itemList);
      this.hotTopics = res.data.data.itemList;
    });
  },
  mounted() {
    new swiper(".swiper", {
      // direction: 'vertical', // 垂直切换选项
      loop: false, // 循环模式选项
      // autoplay: true,
      // 如果需要分页器

      // initialSlide:1,
      // 如果需要前进后退按钮
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    });
  },
  methods: {
    enterCommunity(n) {
      Promise.all([
        this.axios({
          method: "get",
          url: "https://cheyouquan.kakamobi.com/api/web/topic/list-by-publish-time.htm",
          params: {
            clubId: n,
          },
        }),
        this.axios({
          method: "get",
          url: "https://cheyouquan.kakamobi.com/api/web/club/detail.htm",
          params: {
            id: n,
          },
        }),
      ]).then(([res1, res2]) => {
        this.$router.push({
          name: "CommunityView",
          params: {
            newTopic: res2.data.data,
            hotTopics: res1.data.data.itemList,
          },
        });
      });
    },
  },
};
</script>

<style lang="less" scoped>
.JiaoKaoQuan {
  margin-top: 50rem;
  width: 375rem;
  .list-title {
    background-color: white;
    line-height: 40rem;
    border-bottom: 1rem solid #e9e9e9;
    padding-left: 10rem;
    font-size: 17rem;
    font-weight: 700;
    color: #666;
  }
}

.cheyouquan {
  margin-bottom: 10rem;
  background: #fff;
  // padding-bottom: 15rem;
  .title {
    height: 45rem;
    padding: 0 15rem;
    border-bottom: 1px solid #f0f0f0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
      font-size: 16rem;
      line-height: 45rem;
      color: #333;
    }
    p.more {
      color: #999;
      width: 45rem;
      position: relative;
      img {
        width: 7rem;
        display: block;
        position: absolute;
        right: -2rem;
        top: 18rem;
      }
    }
  }
  .swiper {
    // margin-top: 10rem;
    height: 106rem;
    .swiper-wrapper {
      .swiper-slide {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        a {
          width: 25%;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          img {
            width: 52rem;
          }
          span {
            font-size: 12rem;
            color: #666;
            width: 75rem;
            text-align: center;
            margin: 10rem auto 0;
          }
        }

        // &:nth-child(2) {
        // }
      }
    }
  }
}
</style>
