<template>
  <div class="ChooseArea" >
    <transition
      enter-active-class="animate__animated animate__slideInRight animate__faster"
      leave-active-class="animate__animated animate__slideOutRight animate__faster"
    >
      <div class="wrap" v-show="canChooseAarea">
        <div class="province">
          <div class="k-pro" v-for="(proArr, index) in initial" :key="index">
            <span class="zimu">
              {{ proArr[0] }}
            </span>
            <ul>
              <li
                @click="chgPro(pro)"
                v-for="pro in proArr[1]"
                :key="pro.code"
                :class="{
                  changeRed: pro?.province?.code == nowPro?.province?.code,
                }"
              >
                {{ pro.province.name }}
              </li>
            </ul>
          </div>
        </div>

        <ul class="citys">
          <li
            @click="chgCity(city)"
            v-for="city in nowPro.citys"
            :key="city.code"
            :class="{
              changeRed: nowCity?.code == city?.code,
            }"
          >
            {{ city.name }}
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  computed: {
    ...mapState(["data", "nowPro", "nowCity", "canChooseAarea"]),
    initial() {
      return this.data.initial;
    },
  },
  created() {
    // console.log(mapState(["nowPro"]));
  },
  methods: {
    ...mapMutations(["changePro", "changeCity", "closeChooseAarea"]),
    chgPro(n) {
      this.changePro(n);
    },
    // chgCity() {},
    chgCity(n) {
      console.log(n, "(调试)");
      this.changeCity(n);
      this.closeChooseAarea();
    },
    
  },
};
</script>

<style lang="less" scoped>
.ChooseArea {
  .changeRed {
    color: red;
  }
  background-color: rgba(0, 0, 0, 0.8);
  width: 375rem;
  height: 100vh;
  z-index: 5;
  position: fixed;
  top: 0;
  .province {
    position: absolute;
    top: 0;
    height: 100vh;
    width: 112rem;
    background-color: white;
    right: 150rem;
    overflow: auto;
    &::-webkit-scrollbar {
      display: none;
    }
    .zimu {
      background: #f5f5f5;
      display: block;
      height: 30rem;
      line-height: 30rem;
      padding-left: 25rem;
    }
  }
  ul {
    li {
      border-top: 1px solid #e9e9e9;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      height: 40rem;
      line-height: 40rem;
      padding-left: 25rem;
      font-size: 14rem;
    }
  }
  .citys {
    width: 150rem;
    height: 100vh;
    background-color: white;
    position: absolute;
    right: 0;
    overflow: auto;
    &::-webkit-scrollbar {
      display: none;
    }
  }
}
</style>
