<template>
  <div class="KemuVideo">
    <div class="nav">
      <div class="kemu">
        <p>科目</p>
        <span
          data-kemu="2"
          :class="{ active: kemu == 2 }"
          @click="chooseKemu($event)"
          >科目二</span
        ><span
          data-kemu="3"
          :class="{ active: kemu == 3 }"
          @click="chooseKemu($event)"
          >科目三</span
        >
      </div>
      <div class="carType">
        <p>类型</p>
        <span
          data-carType="car"
          :class="{ active: tiku == 'car' }"
          @click="chooseCarType($event)"
          >小车</span
        >
        <span
          data-carType="truck"
          :class="{ active: tiku == 'truck' }"
          @click="chooseCarType($event)"
          >货车</span
        >
        <span
          data-carType="moto"
          :class="{ active: tiku == 'moto' }"
          @click="chooseCarType($event)"
          >摩托车</span
        >
        <span
          data-carType="bus"
          :class="{ active: tiku == 'bus' }"
          @click="chooseCarType($event)"
          >客车</span
        >
      </div>
    </div>
    <ul class="video">
      <li
        v-for="(item, index) in videoInfo"
        @click="
          $router.push({
            name: 'TutorialView',
            params: { item },
          })
        "
        :key="index"
      >
        <div class="imgBox">
          <van-image
            class="van-image"
            :src="'https://apis.netstart.cn/driver' + item?.image"
            width="100rem"
          >
          </van-image>
          <!-- <img :src="'https://apis.netstart.cn/driver' + item?.image" /> -->
          <i>{{ index + 1 }}</i>
          <div class="enter">〉</div>
        </div>
        <div class="title">
          {{ item.title }}
          <div class="duration">
            {{ Math.floor(item.duration / 60) }}:{{ item.duration % 60 }}
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
export default {
  data() {
    return {
      tiku: "moto",
      kemu: 2,
      videoInfo: [],
      loading: true,
    };
  },
  created() {
    if (this.tiku == "moto" || this.tiku == "truck" || this.tiku == "bus") {
      this.tiku = this.carType;
    }
    this.kemu = this.$route.query.kemu;
    this.axios({
      url: `https://apis.netstart.cn/driver/exam/video?tiku=${this.tiku}&kemu=${this.kemu}`,
    }).then((res) => {
      this.videoInfo = res.data.itemList;
    });
  },
  mounted() {
    this.loading = false;
  },
  computed: {
    ...mapState(["carType"]),
    ...mapGetters(["params1"]),
  },
  methods: {
    chooseCarType(event) {
      this.tiku = event.currentTarget.getAttribute("data-carType");
    },
    chooseKemu(event) {
      this.kemu = event.currentTarget.getAttribute("data-kemu");
    },
  },
  watch: {
    params1() {
      if (this.params1.carType == "moto") {
        this.tiku = "moto";
      }
      if (this.params1.carType == "car") {
        this.tiku = "car";
      }
      if (this.params1.carType == "bus") {
        this.tiku = "bus";
      }
      if (this.params1.carType == "truck") {
        this.tiku = "truck";
      }
    },
    tiku() {
      this.axios({
        url: `https://apis.netstart.cn/driver/exam/video?tiku=${this.tiku}&kemu=${this.kemu}`,
      }).then((res) => {
        this.videoInfo = res.data.itemList;
      });
    },
    kemu() {
      this.axios({
        url: `https://apis.netstart.cn/driver/exam/video?tiku=${this.tiku}&kemu=${this.kemu}`,
      }).then((res) => {
        this.videoInfo = res.data.itemList;
      });
    },
  },
};
</script>

<style lang="less" scoped>
.KemuVideo {
  margin-top: 50rem;
  background-color: white;
  img,
  span {
    display: block;
  }
  .active {
    color: rgb(24, 180, 237);
  }
  .nav {
    margin: 50rem 0 5rem 0;
    padding-top: 5rem;
    & > div {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      p {
        margin: 5rem 15rem;
        display: flex;
        font-weight: 800;
        justify-content: center;
        align-items: center;
        color: rgb(65, 191, 254);
        font-size: 15rem;
      }
      span {
        display: flex;
        align-items: center;
        margin: 0 10rem;
        text-align: left;
        width: 36rem;
      }
    }

    .carType {
    }
    .back {
    }
  }
  ul {
    overflow: auto;
    height: 600rem;
    &::-webkit-scrollbar {
      display: none;
    }
    background-color: white;
    li {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      padding: 5rem 0;
      border-top: 1rem solid #f0f0f0;

      .imgBox {
        position: relative;
        z-index: 0;
        .enter {
          position: absolute;
          left: 295%;
          top: 23rem;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 17rem;
          color: #999;
        }
        .van-image {
          width: 100rem;
          margin: 0 10rem;
          min-height: 66.6rem;
        }

        i {
          width: 20rem;
          height: 16rem;
          position: absolute;
          border-bottom-right-radius: 4rem;
          top: 0;
          color: white;
          left: 10rem;
          display: flex;
          justify-content: center;
          align-items: center;
          background-color: rgb(24, 180, 237);
        }
      }
      .title {
        font-size: 16rem;
        margin-top: 5rem;
        font-weight: 550;
        .duration {
          color: #999;
          font-size: 14rem;
          margin-top: 20rem;
          font-weight: 300;
        }
      }
    }
  }
}
</style>
