import Vue from "vue";

const getQuestionsContent = (arr = []) => {
  return Vue.axios.get(`https://apis.netstart.cn/driver/question`, {
    params: {
      ids: (function () {
        let str = JSON.stringify(arr);
        str = str.slice(0, str.length - 1).slice(1);
        return str;
      })(),
    },
  });
};

const getNumList = (mode1, { carType, course, cityCode }) => {
  return Vue.axios({
    url: `https://apis.netstart.cn/driver/exercise/${mode1}`,
    method: "get",
    params: {
      carType,
      course,
      cityCode,
    },
  });
};

const randomNum = (a, b) => {
  let x = Math.round(Math.random() * (b - a)) + a;
  return x;
};

const upsetArr = (arr) => {
  let arr2 = arr.sort(() => {
    return Math.random() - 0.5;
  });
  arr2 = arr2.sort(() => {
    return Math.random() - 0.5;
  });
  arr2 = arr2.sort(() => {
    return Math.random() - 0.5;
  });
  return arr2;
};

export { getQuestionsContent, getNumList, randomNum, upsetArr };
