<script>
import Vuex, { mapActions, mapGetters, mapMutations } from "vuex";
import "swiper/swiper-bundle.css";
import swiper from "swiper";
import JiaXiaoBang from "@/components/home/JiaXiaoBang.vue";
import BotTom from "@/components/BotTom.vue";
import { getNumList } from "../apis/apis";
import store from "../store/index.js";
const { mapState } = Vuex;

// import SwiPer from "@/components/home/SwiPer.vue";
// SwiPer

export default {
  components: { JiaXiaoBang, BotTom },
  data() {
    return {
      biaozhiLink: [
        "list",
        "仪表盘指示灯",
        "新版交警手势",
        "车内功能按键",
        "交通事故图解",
      ],
      theoryTest: [
        "小车(c1)",
        "货车(B2)",
        "摩托车(D)",
        "客车(A1)理论考试",
        "客运(KY)",
        "货运(HY)",
        "危险品(WX)",
        "教练员(JL)",
        "出租车(CZ)",
      ],
      cTypes: [
        "car",
        "truck",
        "moto",
        "bus",
        "keyun",
        "huoyun",
        "weixian",
        "jiaolian",
        "chuzu",
      ],
      zTypes: ["keyun", "huoyun", "weixian", "jiaolian", "chuzu"],
      cheyouquanNum: [351, 347, 348, 349, 350, 345, 341, 353],
      modes: ["sequence", "chapters", "special", "exam"],
      nav: ["找驾校", "找教练", "找陪练", "驾考圈", "软件下载"],
      nav2: ["驾考宝典企业版", "智慧驾校", "买车网", "平行之家", "二手车"],
      navPy: [
        "zhaojiaxiao",
        "zhaojiaolian",
        "zhaopeilian",
        "jiakaoquan",
        "ruanjianxiazai",
      ],
      navPy2: [
        "jiakaobaodianqiyeban",
        "zhihuijiaxiao",
        "maichewang",
        "pinghangzhijia",
        "ershouche",
      ],
      tiaozhuan: [
        `https://appstore-paas.jiakaobaodian.com/base/?_v=TU-5.2.0-09292102#/login?redirectUrl=https%3A%2F%2Fqiye.jiakaobaodian.com%2F`,
        `https://www.futurephantom.cn/`,
        `https://m.maiche.com/rank/`,
        `https://www.pingxing.cn/`,
        `https://m.xiaozhu2.com/`,
      ],
      lianxi: ["顺序练习", "章节练习", "专项练习", "模拟考试"],
      cheyouquan: [
        "考前祈愿",
        "科目一",
        "科目二",
        "科目三",
        "科目四",
        "晒驾照",
        "校花校草",
        "学车趣事",
        "学车问答",
      ],
      jiaotongbiaozhi: [
        "交通标志",
        "仪表盘指示灯",
        "新版交警手势",
        "车内功能按钮",
        "交通事故图解",
      ],
      jiaotongbiaozhiTo: ["TrafficSigns/light"],
    };
  },
  computed: {
    ...mapState([
      "showInKemu",
      "nowCity",
      "paihangbang",
      `jiaolianbang`,
      `mode1`,
      "allQuestionsNumber",
      "carType",
    ]),
    ...mapGetters(["params1"]),
    kaoshiType() {
      return this.theoryTest[this.cTypes.findIndex((o) => o == this.carType)];
    },
  },
  created() {
    this.showLogo();
    getNumList("sequence", this.params1).then((res) => {
      // console.log(res.data.questionIds);
      store.dispatch("initialize", {
        allQtNum: [...res.data.questionIds],
        skip: false,
      });
    });
    this.updateActiveIndex(0);
  },

  mounted() {
    new swiper(".swiper", {
      // direction: 'vertical', // 垂直切换选项
      loop: false, // 循环模式选项
      autoplay: true,
      // 如果需要分页器
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      // initialSlide:1,
      // 如果需要前进后退按钮
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    });

    new swiper(".swiper2", {
      // direction: 'vertical', // 垂直切换选项
      loop: false, // 循环模式选项
      autoplay: true,
      // 如果需要分页器
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      // 如果需要前进后退按钮
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    });
    this.updateActiveIndex(0);
  },
  destroyed() {
    this.hideLogo();
  },
  methods: {
    enterCommunity(n) {
      Promise.all([
        this.axios({
          method: "get",
          url: "https://cheyouquan.kakamobi.com/api/web/topic/list-by-publish-time.htm",
          params: {
            clubId: n,
          },
        }),
        this.axios({
          method: "get",
          url: "https://cheyouquan.kakamobi.com/api/web/club/detail.htm",
          params: {
            id: n,
          },
        }),
      ]).then(([res1, res2]) => {
        this.$router.push({
          name: "CommunityView",
          params: {
            newTopic: res2.data.data,
            hotTopics: res1.data.data.itemList,
          },
        });
      });
    },
    ...mapActions(["initialize"]),
    toPractice(index, kemu) {
      this.changeCouse(kemu);
      console.log(index);
      if (index == 0) {
        this.changeMode("sequence");
        getNumList(this.mode1, this.params1).then((res) => {
          store.dispatch("initialize", {
            allQtNum: [...res.data?.questionIds],
          });
        });
      }
      if (index == 1) {
        this.changeMode("chapters");
        this.axios({
          method: "get",
          url: `https://apis.netstart.cn/driver/exercise/chapters`,
          params: {
            carType: this.params1.carType,
            course: "kemu1",
            cityCode: this.nowCity.code,
            kemu: 1,
          },
        }).then((res) => {
          console.log(res.data.chapters);
          this.$router.push({
            name: "ChaptersView",
            params: {
              list: [...res.data.chapters],
            },
          });
        });
      }
      if (index == 2) {
        store.dispatch("initialize", {
          allQtNum: this.allQuestionsNumber,
          upset: true,
        });
      }
      if (index == 3) {
        this.isExaming(true);
        store.dispatch("initialize", {
          allQtNum: this.allQuestionsNumber.slice(100, 200),
          upset: true,
        });
      }

      // $router.push({ path: `/answer` });
    },
    biaozhi(n) {
      if (n != "list") {
        this.axios({
          method: "get",
          url: `${n}.json`,
        })
          .then((res) => {
            // console.log(res.data.data.itemList);
            this.$router.push({
              name: "SignView",
              params: { arr: res.data.data.itemList },
            });
          })
          .catch((res) => {
            res;
          });
      } else if (n == "list") {
        this.$router.push({ path: "TrafficSigns" });
      }
    },
    lookVedio(n) {
      this.$router.push({ path: "/KemuVideo", query: { kemu: n } });
    },
    ...mapMutations([
      "updatePaihangbang",
      "isExaming",
      "hideLogo",
      "showLogo",
      "changeCouse",
      "changeMode",
      "UpdateNowDijiti",
      "updateActiveIndex",
    ]),
  },
};
</script>

