<template>
  <div class="KumuExercise">
    <div class="jiakaolianxi">
      <div class="title">驾考练习</div>
      <div class="items">
        <a
          @click="toPractice(index)"
          v-for="(item, index) in items"
          :key="index"
        >
          <img
            :src="require(`../../assets/KumuExercise/${items[index]}.png`)"
          />
          <span>{{ items[index] }}</span>
        </a>
      </div>
    </div>
    <div class="keyitupo">
      <div class="title">科目一考试及难点突破</div>
      <div class="down">
        <div class="moni">
          <img src="../../assets/KumuExercise/模拟考试.png" />
          <div class="text" @click="toTest">模拟考试</div>
        </div>
        <div class="jilu" @click="$router.push({path:'/TestRecord'})">
          <img src="../../assets/KumuExercise/考试记录.png" />
          <div class="text">考试记录</div>
        </div>
      </div>
    </div>
    <div class="yuyuekaoshi">
      <div class="title">预约考试</div>
      <div class="down">
        <div class="left">
          <p>考试预约</p>
          <span>在线快捷考试预约入口 ></span>
        </div>
        <img src="../../assets/KumuExercise/考试预约.png" />
      </div>
    </div>
    <div class="navigation-c x-scorll show">
      <a>首页</a> >
      <a href="https://m.jiakaobaodian.com/mnks/guangzhou.html"
        >广州市模拟考试</a
      >
      >
      <a href="https://m.jiakaobaodian.com/mnks/kemu1/bus-guangzhou.html"
        >客车科目一</a
      >
    </div>
    <BotTom></BotTom>
  </div>
</template>

<script>
import BotTom from "@/components/BotTom.vue";
import { mapGetters, mapMutations, mapState } from "vuex";
// import Vue from 'vue'
import store from "../../store/index.js";
import { getNumList } from "../../apis/apis";

export default {
  components: { BotTom },
  data() {
    return {
      items: [
        "顺序练习",
        "随机练习",
        "章节练习",
        "专项练习",
        "难题练习",
        "我的收藏",
        "我的错题",
        "未做题",
      ],

      router1: [
        "sequence",
        "chapters",
        "special",
        "exam",
        "difficulty",
        "wodeshoucang",
        "wodecuoti",
        "weizuoti",
      ],
    };
  },
  computed: {
    ...mapState(["mode1", "allQuestionsNumber", "course", "nowCity"]),
    ...mapGetters(["params1"]),
  },
  methods: {
    toTest(n) {
      console.log(n, "(调试)");
      this.isExaming(true);
        store.dispatch("initialize", {
          allQtNum: this.allQuestionsNumber.slice(100, 200),
          upset: true,
        });
    },
    ...mapMutations(["changeMode","isExaming"]),
    toPractice(index) {
      if (index == 0) {
        this.changeMode("sequence");
        getNumList(this.mode1, this.params1).then((res) => {
          console.log(res.data.questionIds);
          store.dispatch("initialize", {
            allQtNum: [...res.data.questionIds],
          });
        });
      }
      if (index == 1 || index == 3 || index == 4 || index == 7) {
        store.dispatch("initialize", {
          allQtNum: this.allQuestionsNumber,
          upset: true,
        });
      }
      if (index == 2) {
        this.changeMode("chapters");
        this.axios({
          method: "get",
          url: `https://apis.netstart.cn/driver/exercise/chapters`,
          params: {
            carType: this.params1.carType,
            course: this.course,
            cityCode: this.nowCity.code,
            kemu: 1,
          },
        }).then((res) => {
          console.log(res.data.chapters);
          this.$router.push({
            name: "ChaptersView",
            params: {
              list: [...res.data.chapters],
            },
          });
        });
      }
      if (index == 5) {
        this.$router.push({
          name: "AnswerPage",
          params: { local: "collect" },
        });
      }
      if (index == 6) {
        this.$router.push({
          name: "AnswerPage",
          params: { local: "mistakes" },
        });
      }
    },
  },
};
</script>

<style lang="less" scoped>
.KumuExercise {
  width: 375rem;
  margin-top: 60rem;
  .title {
    height: 52rem;
    line-height: 52rem;
    padding-left: 12.5rem;
    margin-top: 10rem;
    border-bottom: 1px solid #f0f0f0;
    font-size: 17rem;
  }
  .jiakaolianxi {
    background-color: white;
    .items {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      a {
        width: 25%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 112rem;
        img {
          width: 29rem;
        }
        span {
          font-size: 14rem;
          display: block;
          margin-top: 10rem;
        }
      }
    }
  }
  .keyitupo {
    background-color: white;
    .down {
      height: 156rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .moni {
        height: 156rem;
        border-right: 1px solid #f0f0f0;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 50%;
        img {
          width: 90rem;
        }
        .text {
          margin: 0 auto;
          display: block;
          width: 90rem;
          height: 30rem;
          border-radius: 5rem;
          line-height: 30rem;
          color: #fff;
          background-color: #18b4ed;
          text-align: center;
          font-size: 13rem;
        }
      }
    }
  }

  .jilu {
    width: 50%;
    height: 156rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    img {
      width: 36rem;
      padding: 0 20rem 0 10rem;
      display: block;
      box-sizing: content-box;
    }
    .text {
      font-size: 14rem;
    }
  }
  .yuyuekaoshi {
    background-color: white;
    .down {
      height: 102rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left {
        margin-left: 15rem;
        p {
          font-size: 18rem;
          font-weight: 600;
          margin: 5rem 0;
        }
        span {
          font-size: 14rem;
          color: #333;
          word-spacing: 5rem;
        }
      }
      img {
        display: block;
        width: 76rem;
        margin-right: 17rem;
      }
    }
  }
  .navigation-c {
    white-space: nowrap;
    height: 100%;
    padding-left: 10rem;
    overflow: scroll;
    border-bottom: solid 1px #eee;
    a {
      font-size: 14rem;
      line-height: 30rem;
      padding-right: 5rem;
      color: #666;
    }
  }
}
</style>
