<template>
  <div class="PaihangJuling">
    <div class="nav">
      <p
        @click="upset(index)"
        v-for="(nav, index) in navs"
        :key="index"
        :class="{ active: index == gaoliang }"
      >
        {{ nav }}
      </p>
      <div class="more">更多<img src="../../assets/找驾校/更多.png" /></div>
    </div>
  </div>
</template>

<script>
import Vuex from "vuex";
const { mapMutations } = Vuex;
export default {
  data() {
    return {
      navs: ["排行", "距离", "口碑", "价格"],
      gaoliang: 0,
    };
  },
  methods: {
    ...mapMutations(["randomCoach"]),
    upset(index) {
      this.gaoliang = index;
      this.randomCoach();
    },
  },
};
</script>

<style lang="less" scoped>
.PaihangJuling {
  margin-top: 50rem;

  .nav {
    height: 44rem;
    font-size: 13rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: white;
    p {
      flex-shrink: 0;
      width: 55rem;
      background-size: 100%;
      display: flex;
      line-height: 26rem;
      justify-content: center;
      align-items: center;
      border-radius: 13rem;
      color: #666;
      background-color: #f4f7f7;
    }
    .active {
      background-image: linear-gradient(90deg, #00e0e5 4%, #0086fa 95%);
      color: #fff;
    }
    .more {
      flex-shrink: 0;

      display: flex;
      justify-content: center;
      align-items: center;
      img {
        display: block;
        width: 10rem;
        margin: 3rem;
        margin-bottom: 0;
      }
    }
  }
}
</style>
