<template>
  <div class="TestType" @click="close($event)">
    <!-- <transition
      enter-active-class="animate__animated animate__fadeIn animate__faster"
      leave-active-class="animate__animated animate__fadeOut animate__faster"
    >
      <div class="mask" v-show="canChooseTestType"></div>
    </transition> -->
    <transition
      enter-active-class="animate__animated animate__slideInRight animate__faster"
      leave-active-class="animate__animated animate__slideOutRight animate__faster"
    >
      <div class="wrap" v-show="canChooseTestType">
        <div class="register">
          <a href="">注册登录</a>
          <div class="tip">登录后电脑和APP可云同步做题进度</div>
        </div>
        <div class="typewrap">
          <div class="title-c">驾考类型</div>
          <ul class="jiakaoTpye type">
            <li
              @click="
                changeCarType($event.currentTarget.dataset.type),
                  changeShowInKemu(item)
              "
              :data-type="cTypes[index]"
              v-for="(item, index) in carTypes"
              :key="index"
            >
              <img :src="require(`../assets/homePic/` + item + `.png`)" />
              <span>{{ item }}</span>
              <div class="tick" v-if="cTypes[index] == carType">
                <img src="../assets/homePic/高亮打勾.png" />
              </div>
            </li>
          </ul>
          <div class="zigeType">
            <div class="title-c">资格证类型</div>
            <ul class="zigezhengTpye type">
              <li
                @click="changeCarType($event.currentTarget.dataset.type)"
                :data-type="zTypes[index]"
                v-for="(item, index) in zigeTpyes"
                :key="index"
              >
                <img :src="require(`../assets/homePic/` + item + `.png`)" />
                <span>{{ item }}</span>
                <div class="tick" v-if="zTypes[index] == carType">
                  <img src="../assets/homePic/高亮打勾.png" />
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  data() {
    return {
      cTypes: ["car", "truck", "moto", "bus"],
      zTypes: ["keyun", "huoyun", "weixian", "jiaolian", "chuzu"],
    };
  },
  computed: {
    ...mapState(["carType", "canChooseTestType", "carTypes", "zigeTpyes"]),
  },
  methods: {
    close(n) {
      console.log(n.target.className, "(调试)");
      if ('TestType' == n.target.className) {
        this.closeChooseTestType();
      }
    },
    ...mapMutations([
      "changeCarType",
      "changeShowInKemu",
      "closeChooseTestType",
    ]),
  },
};
</script>

<style lang="less" scoped>
.TestType {
  z-index: 5;
  width: 375rem;
  height: 667rem;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;

  // .mask {
  //   width: 375rem;
  //   height: 100vh;
  //   background-color: rgba(0, 0, 0, 0.8);
  //   top: 0;
  //   z-index: 5;
  //   right: 0;
  // }
  .wrap {
    width: 300rem;
    height: 100vh;
    background-color: white;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 5;
    .register {
      padding: 19rem 14rem;
      a {
        width: 85rem;
        height: 25rem;
        text-decoration: none;
        color: rgb(55, 181, 248);
        background-image: url("@/assets/homePic/注册登录.png");
        background-repeat: no-repeat;
        background-position: left center;
        text-align: right;
        font-size: 15rem;
        background-size: 18rem;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-shrink: 0;
      }
      .tip {
        font-size: 13rem;
        color: #999;
        line-height: 1.3;
        text-align: left;
        margin-top: 10rem;
      }
    }
    .typewrap {
      padding: 0 14rem;
      .title-c {
        height: 40rem;
        border-bottom: solid 1px #f0f0f0;
        display: flex;
        align-items: center;
        font-size: 15rem;
      }
    }
    ul {
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      align-items: center;
      padding-top: 5rem;
      li {
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 80rem;
        position: relative;
        img {
          width: 45rem;
        }
        span {
          color: #666;
          text-align: center;
          font-size: 13rem;
        }
        .tick {
          position: absolute;
          width: 17rem;
          right: 20rem;
          top: 5rem;
        }
      }
    }
    .zigezhengTpye {
      img {
        width: 65rem;
      }
      .tick {
        position: absolute;
        top: 10rem;
      }
    }
  }
}
</style>
