<template>
  <div class="TestRecord">
    <div class="wrap">
      <div class="title">
        <div class="text">考试记录</div>
        <div class="clear" @click="log123">清除全部</div>
      </div>
      <div class="highest" v-show="!empty">
        <div class="imgbox"><img src="../../assets/答题/zuijia2.png" /></div>
        <p>您的最高历史成绩为 {{ zuigaofen }} 分</p>
      </div>
      <ul>
        <li v-for="(item, index) in testRecord" :key="index">
          <div class="score">{{ item.score }}分</div>
          <div class="right">
            <div class="designation">{{ item.designation }}</div>
            <div class="time">{{ item.testTime }}</div>
          </div>
        </li>
      </ul>
      <div class="empty" v-show="empty">
        <div class="imgbox">
          <img src="../../assets/答题/empty.png" />
          <p>空空如也</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      testRecord: JSON.parse(localStorage.getItem("testRecord")) || [],
    };
  },
  methods: {
    log123() {
      this.testRecord = [];
      localStorage.setItem("testRecord", JSON.stringify([]));
    },
  },
  computed: {
    empty() {
      if (this.testRecord.length == 0) {
        return 1;
      } else {
        return 0;
      }
    },
    zuigaofen() {
      if (this.testRecord.length == 0) {
        return 0;
      } else {
        let zuigao = [];
        this.testRecord.forEach((ele) => {
          zuigao.push(ele.score);
        });
        let x = Math.max(...zuigao);
        return x;
      }
    },
  },
  watch: {
    testRecord() {
      localStorage.setItem("testRecord", JSON.stringify(this.testRecord));
    },
  },
};
</script>

<style lang="less" scoped>
.TestRecord {
  // padding: 10rem;
  margin-top: 50rem;
  height: 617rem;
  background-color: white;
  .wrap {
    .title {
      padding: 10rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1rem solid #e9e9e9;

      .text {
        font-size: 16rem;
        font-weight: 700;
      }
      .clear {
        border: 1rem solid #c0c0c0;
        color: #727272;
        padding: 5rem 8rem;
        border-radius: 5rem;
      }
    }
    .highest {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20rem 10rem;
      padding-bottom: 0;
      .imgbox {
        margin-right: 20rem;
        background: url("../../assets/答题/zuijia.png") center no-repeat;
        background-size: cover;
        height: 80rem;
        width: 80rem;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        img {
          display: block;
        }
      }
      p {
        font-size: 25rem;
        letter-spacing: 2rem;
        font-weight: 600;
        width: 180px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: 100%;
        color: #2b2b2b;
      }
    }
  }
  ul {
    padding: 20rem 10rem;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1rem solid #e3e2e2;
      padding: 10rem;
      .score {
        color: rgb(242, 94, 94);
        letter-spacing: 1rem;
        font-size: 20rem;
        font-weight: 600;
      }
      .right {
        text-align: right;

        .designation {
          color: rgb(242, 94, 94);
          margin-bottom: 8rem;
          width: 100%;
          font-size: 14rem;
        }
        .time {
          color: #727272;
        }
      }
    }
  }
  .empty {
    margin-top: 100rem;
    height: 100%;

    .imgbox {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      img {
        display: block;
      }
    }
    p {
      font-size: 20rem;
      color: rgb(152, 152, 152);
      letter-spacing: 6rem;
      margin-top: 25rem;
    }
  }
}
</style>
