<template>
  <div class="SignView">
    <DetailsLunbo
      @close-Lunbo="closeLunbo"
      v-show="showLunbo != 'close'"
      :arr="arr"
      :showLunbo="showLunbo"
    ></DetailsLunbo>

    <ul>
      <ZhiShiDeng
        @click.native="openLunbo(index)"
        v-for="(item, index) in arr"
        :key="index"
        :item="item"
      ></ZhiShiDeng>
    </ul>
  </div>
</template>

<script>
import ZhiShiDeng from "../../components/Trafficsign/ZhiShiDeng.vue";
import DetailsLunbo from "../../components/Trafficsign/DetailsLunbo.vue";
export default {
  components: {
    ZhiShiDeng,
    DetailsLunbo,
  },
  data() {
    return {
      arr: [],
      showLunbo: "close",
    };
  },
  created() {
    this.arr = this.$route.params.arr;
  },
  methods: {
    closeLunbo() {
      this.showLunbo = "close";
    },
    openLunbo(n) {
      console.log(n);
      this.showLunbo = n;
    },
  },
};
</script>

<style lang="less" scoped>
.SignView {
  max-height: 667rem;
  overflow: auto;
  &::-webkit-scrollbar {
    display: none;
  }
  margin-top: 50rem;
  ul {
    display: flex;
    justify-content: flex-start;
    // align-items: center;
    flex-wrap: wrap;
    // margin:  5rem;
    padding: 0 12rem;
  }
  .DetailsLunbo {
    position: fixed;
    top: 0;
  }
}
</style>
