<template>
  <div class="ChaptersView">
    <ul class="wrap">
      <li
        v-for="(item, index) in list"
        @click="enterTraining(item.id)"
        :key="index"
      >
        <i>{{ index + 1 }}</i>
        <h3>{{ item.title }}</h3>
        <span>{{ item.count }}</span>
        <div class="enter"></div>
      </li>
    </ul>
    <BotTom></BotTom>
  </div>
</template>

<script>
import BotTom from "@/components/BotTom.vue";
import store from "../../store/index.js";

import { mapMutations, mapGetters, mapState } from "vuex";
// import store from '../../store/index.js'
export default {
  components: {
    BotTom,
  },
  data() {
    return {
      list: [],
    };
  },
  computed: {
    ...mapGetters(["params1"]),
    ...mapState(["allQuestionsNumber"]),
  },
  created() {
    this.list =
      this.$route.params.list ||
      JSON.parse(localStorage.getItem("chaptersList"));
    localStorage.setItem("chaptersList", JSON.stringify(this.list));
  },
  methods: {
    ...mapMutations(["randomAll"]),
    enterTraining(n) {
      this.axios({
        method: "get",
        url: `https://apis.netstart.cn/driver/exercise/chapter/${n}`,
      }).then((res) => {
        console.log(res.questionIds);
        if (res.questionIds) {
          store.dispatch("initialize", {
            allQtNum: res.questionIds,
          });
        } else {
          // console.log(this.allQuestionsNumber);
          store.dispatch("initialize", {
            allQtNum: this.allQuestionsNumber,
            upset: true,
          });
        }
      });
    },
  },
  watch: {
    params1() {
      this.axios({
        method: "get",
        url: `https://apis.netstart.cn/driver/exercise/chapters`,
        params: this.params1,
      }).then((res) => {
        console.log(res.data.chapters);
        this.list = res.data.chapters;
      });
      localStorage.setItem("chaptersList", JSON.stringify(this.list));
    },
  },
};
</script>

<style lang="less" scoped>
.ChaptersView {
  margin-top: 50rem;
  .com-layout-footer {
    position: fixed;
    bottom: 0;
  }
  ul {
    height: 490rem;
    overflow: auto;
    &::-webkit-scrollbar {
      display: none;
    }
    padding: 0 10rem;
    background-color: white;
    li {
      font-size: 13rem;
      display: flex;
      justify-content: start;
      align-items: center;
      position: relative;
      z-index: 0;
      height: 48rem;
      border-bottom: 1rem solid rgb(245, 245, 245);
      i {
        display: block;
        width: 20rem;
        height: 18rem;
        color: white;
        background-color: rgb(24, 180, 237);
        border-radius: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      h3 {
        margin: 0 8rem;
        font-size: 16rem;
        max-width: 240rem;
        overflow: hidden;
      }
      span {
        display: block;
        color: #999;
      }
      .enter {
        position: absolute;
        z-index: 0;
        width: 5rem;
        height: 48rem;
        background: url("../../assets/KumuExercise/enter.png") center no-repeat;
        background-size: 6rem;
        right: 0;
      }
    }
  }
}
</style>
