<template>
  <div class="ChoosePanel" @click="disapearPanel($event.target)">
    <transition
      enter-active-class="animate__animated animate__slideInUp animate__faster"
      leave-active-class="animate__animated animate__slideOutDown animate__faster"
    >
      <div class="wrap" v-show="showPanel">
        <div class="title">
          <div class="dadui">
            <span>答对</span>
            <span class="daduishu">{{ dui }}</span>
          </div>
          <div class="dacuo">
            <span>答错</span>
            <span class="dacuoshu">{{ cuo }}</span>
          </div>
          <div class="weidati">
            <span>未答题</span>
            <span class="weidashu">{{ weidati }}</span>
          </div>
        </div>
        <ul class="panel">
          <li
            v-for="(item, index) in allQuestionsNumber"
            :class="{
              red: recordYesNo[item] == 'red',
              blue: recordYesNo[item] == 'blue',
              active: nowDijiti - 1 == index,
            }"
            :key="index"
            @click="zhongtuChoose(index)"
          >
            {{ index + 1 }}
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import { getQuestionsContent } from "../../apis/apis";

export default {
  props: ["showPanel", "activeIndex", "recordYesNo", "dui", "cuo", "weidati"],
  data() {
    return {
      data1: [],
      data2: [],
      data3: [],
    };
  },
  computed: {
    ...mapState([
      "allQuestionsNumber",
      "getTenQuestions",
      "nowDijiti",
      "showQuestions",
      "loadedQuestion",
    ]),

    arr() {
      let arr1 = [];
      for (let i = 1; i < 99; i++) {
        arr1.push(i);
      }
      return arr1;
    },
  },
  created() {},
  methods: {
    ...mapMutations([
      "updateGetTenQuestions",
      "updateShowQuestions",
      "UpdateNowDijiti",
      "updateLoadedQuestion",
    ]),
    disapearPanel(n) {
      // console.log(n.className);
      if (n.className == "ChoosePanel") {
        this.$emit("disapear-Panel");
      }
    },
    tenRange(n) {
      let first;
      let end;
      if (n % 10 !== 0) {
        first = n - (n % 10);
        end = n - (n % 10) + 10;
      } else {
        first = n;
        end = n + 10;
      }
      return { first, end };
    },

    zhongtuChoose(n) {
      // console.log(this.loadedQuestion[n]);
      console.log(n);

      if (this.loadedQuestion[n] == 0) {
        let { first, end } = this.tenRange(n);
        getQuestionsContent(this.allQuestionsNumber.slice(first, end)).then(
          (res) => {
            console.log("res1:", res.data);
            this.updateLoadedQuestion({ gotTenQuestions: [...res.data] });
            if (this.loadedQuestion[n - 2] == 0) {
              let q = n - 2;
              let { first, end } = this.tenRange(q);
              getQuestionsContent(
                this.allQuestionsNumber.slice(first, end)
              ).then((res2) => {
                console.log("res2:", res2.data);
                this.updateLoadedQuestion({ gotTenQuestions: [...res2.data] });
                this.updateShowQuestions(
                  this.loadedQuestion.slice(n - 1, n + 2)
                );
                this.$emit("skip-lunbo", 1);
              });
            } else if (this.loadedQuestion[n + 2] == 0) {
              let h = n + 2;
              let { first, end } = this.tenRange(h);
              getQuestionsContent(
                this.allQuestionsNumber.slice(first, end)
              ).then((res3) => {
                console.log("res3:", res3.data);
                this.updateLoadedQuestion({ gotTenQuestions: [...res3.data] });
              });
              this.updateShowQuestions(this.loadedQuestion.slice(n - 1, n + 2));
              this.$emit("skip-lunbo", 1);
            } else {
              this.updateShowQuestions(this.loadedQuestion.slice(n - 1, n + 2));
              this.$emit("skip-lunbo", 1);
            }
          }
        );
      } else {
        if (this.loadedQuestion[n - 2] == 0) {
          let q = n - 2;
          let { first, end } = this.tenRange(q);
          getQuestionsContent(this.allQuestionsNumber.slice(first, end)).then(
            (res2) => {
              console.log("res2:", res2.data);
              this.updateLoadedQuestion({ gotTenQuestions: [...res2.data] });
              this.updateShowQuestions(this.loadedQuestion.slice(n - 1, n + 2));
              this.$emit("skip-lunbo", 1);
            }
          );
        } else if (this.loadedQuestion[n + 2] == 0) {
          let h = n + 2;
          let { first, end } = this.tenRange(h);
          getQuestionsContent(this.allQuestionsNumber.slice(first, end)).then(
            (res3) => {
              console.log("res3:", res3.data);
              this.updateLoadedQuestion({ gotTenQuestions: [...res3.data] });
            }
          );
          this.updateShowQuestions(this.loadedQuestion.slice(n - 1, n + 2));
          this.$emit("skip-lunbo", 1);
        } else {
          this.updateShowQuestions(this.loadedQuestion.slice(n - 1, n + 2));
          this.$emit("skip-lunbo", 1);
        }
      }

      this.$emit("disapear-Panel");
    },
  },
};
</script>

<style lang="less" scoped>
.ChoosePanel {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.795);
  width: 375rem;
  height: calc(100vh - 50rem);
  bottom: 0;
  z-index: 2;
  .wrap {
    width: 100%;
    height: 560rem;
    background-color: white;
    position: absolute;
    bottom: 0;
    padding: 0 15rem 50rem 15rem;

    .title {
      height: 45rem;
      display: flex;
      justify-content: flex-startd;
      align-items: center;
      border-bottom: 1rem solid #f0f0f0;
      font-size: 14rem;
      .dadui,
      .dacuo,
      .weidati {
        margin-right: 50rem;
        span {
          &:nth-child(1) {
            margin-right: 10rem;
          }
        }
        .daduishu {
          color: #18b4ed;
        }
        .dacuoshu {
          color: #f25e5e;
        }
        .weidashu {
          color: #999;
        }
      }
    }
    .panel {
      display: flex;
      justify-content: flex-start;
      // align-items: flex-start;
      align-content: flex-start;
      padding-bottom: 10rem;
      flex-wrap: wrap;
      height: 515rem;
      overflow: auto;
      &::-webkit-scrollbar {
        display: none;
      }

      li {
        flex-shrink: 0;
        width: 45rem;
        height: 36rem;
        color: white;
        background-color: #d2d2d2;
        border-radius: 4rem;
        margin: 15rem 0 0 15rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 14rem;
        &:nth-child(6n + 1) {
          margin: 15rem 0 0 0rem;
          // background-color: #f25e5e;
        }
      }
      .active {
        border: 2rem solid gray;
      }
      .blue {
        background-color: rgb(24, 180, 237);
      }
      .red {
        background-color: rgb(242, 94, 94);
      }
    }
  }
}
</style>
