<template>
  <li class="HotTopic" @click="toDetail(hotTopic.topicId)">
    <a
      ><div class="author-container">
        <div class="img-w">
          <img :src="hotTopic.author.avatar" />
        </div>
        <div class="text">
          <span class="name">{{ hotTopic.author.name }}</span
          ><span class="city"
            >来自：<span class="blue">
              {{ hotTopic.author.cityName }}</span
            ></span
          >
        </div>
      </div>
      <div class="title word-break">
        <span
          ><span class=""></span
          ><span class="title-text">{{ hotTopic.summary }}</span></span
        >
      </div>
      <div class="img-container clearfix" v-if="hotTopic.imageList.length">
        <div
          class="img-w"
          v-for="(image, index) in hotTopic.imageList"
          :key="index"
        >
          <img :src="image.list.url" />
        </div>
      </div>
      <div class="topic-address clearfix">
        <span class="a-l">{{
          new Date(parseInt(hotTopic.author.createTime))
            .toLocaleString()
            .replace(/:\d{1,2}$/, " ")
            .split(" ")[0]
            .replace("/", "-")
            .replace("/", "-")
        }}</span
        ><span class="a-r hufu-count">{{ hotTopic.commentCount }}</span>
      </div></a
    >
  </li>
</template>

<script>
export default {
  props: ["hotTopic"],
  methods: {
    toDetail(n) {
      this.axios({
        method: "get",
        url: "https://cheyouquan.kakamobi.com/api/web/topic/detail.htm",
        params: {
          id: n,
        },
      }).then((res) => {
        if (res.data.data.content) {
          this.$router.push({
            name: "PostDetail",
            params: { details: res.data.data },
          });
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
  // overflow: hidden;
}
.HotTopic {
  background-color: white;
  padding: 10rem 15rem;
  border-bottom: 1rem solid #e9e9e9;
  // height: 209rem;
  & > a {
    display: block;
    width: 100%;
    & > .author-container {
      margin-bottom: 10rem;
      & > .img-w {
        float: left;
        display: block;
        width: 40rem;
        height: 40rem;
        overflow: hidden;
        border-radius: 50%;
        img {
          display: block;
          width: 40rem;
          height: auto;
        }
      }
      & > .text {
        line-height: 40rem;
        margin-left: 1rem;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .name {
          display: inline-block;
          font-size: 16rem;
          padding: 0 15rem;
          color: #666;
          white-space: nowrap;
          max-width: 220rem;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .city {
          // min-width: 70rem;
          font-size: 0.28rem;
          color: #999;
          float: right;
          .blue {
            color: #7eb4ed;
          }
        }
      }
    }
    .title {
      line-height: 25rem;
      font-size: 18rem;
      .title-text {
        display: -webkit-box;
        margin-right: 5rem;
        // max-height: 50rem;
        overflow: hidden;
        white-space: 2;
        text-overflow: ellipsis;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
    }

    & > .img-container {
      margin: 11rem 0;
      height: 50rem;
      max-width: 345rem;
      min-height: 50rem;
      overflow: hidden;
      .img-w {
        float: left;
        display: block;
        width: 50rem;
        height: 50rem;
        overflow: hidden;
        margin-right: 10rem;
        img {
          display: block;
          width: 50rem;
          height: auto;
        }
      }
    }

    & > .topic-address {
      font-size: 12rem;
      color: #999;
      margin-top: 9rem;
      line-height: 1.2;
      display: flex;
      justify-content: space-between;
      align-items: center;
      & > .a-l {
        float: left;
        margin-right: 10rem;
      }
      .hufu-count {
        display: inline-block;
        background: url("../../assets/cheyouquan/评论.png") center left
          no-repeat;
        background-size: contain;
        padding-left: 20rem;
      }
    }
  }
}
</style>
