<template>
  <div class="CommunityView">
    <div class="header2">
      <div class="wrap">
        <div class="left">
          <img :src="newTopic.iconUrl" />
        </div>
        <div class="right">
          <div class="name">{{ newTopic?.name }}社区</div>
          <div class="desc">{{ newTopic?.desc }}</div>
        </div>
      </div>
    </div>
    <div class="newTopic">
      <div class="zuixin">最新话题</div>
      <ul class="topics">
        <li
          v-for="(notice, index) in newTopic.noticeList"
          :key="index"
          @click="toDetail(notice.topicId)"
        >
          <span class="ding">顶</span>
          <h5>{{ notice.title }}</h5>
        </li>
      </ul>
    </div>
    <ul class="topicContainer">
      <HotTopic
        v-for="(hotTopic, index) in hotTopics"
        :key="index"
        :hotTopic="hotTopic"
      ></HotTopic>
    </ul>
  </div>
</template>

<script>
import HotTopic from "@/components/cheyouquan/HotTopic.vue";

export default {
  components: { HotTopic },
  data() {
    return {
      newTopic: JSON.parse(localStorage.getItem("newTopic")) || {},
      hotTopics: [],
    };
  },
  created() {
    this.newTopic =
      this.$route.params.newTopic ||
      JSON.parse(localStorage.getItem("newTopic"));
    localStorage.setItem("newTopic", JSON.stringify(this.newTopic));
    this.hotTopics =
      this.$route.params.hotTopics ||
      JSON.parse(localStorage.getItem("hotTopics"));
    localStorage.setItem("hotTopics", JSON.stringify(this.hotTopics));
  },
  methods: {
    toDetail(n) {
      this.axios({
        method: "get",
        url: "https://cheyouquan.kakamobi.com/api/web/topic/detail.htm",
        params: {
          id: n,
        },
      }).then((res) => {
        if (res.data.data.content) {
          this.$router.push({
            name: "PostDetail",
            params: { details: res.data.data },
          });
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.CommunityView {
  margin-top: 50rem;
  img,
  span {
    display: block;
  }

  .header2 {
    height: 100rem;
    background-color: white;
    margin-bottom: 10rem;
    .wrap {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      padding: 15rem;
      .left {
        img {
          width: 70rem;
          margin-right: 15rem;
        }
      }
      .right {
        margin-top: 3rem;

        .name {
          color: #333;
          font-size: 17rem;
          font-weight: 700;
        }
        .desc {
          color: #999;
          font-size: 14rem;
          margin-top: 5rem;
        }
      }
    }
  }
  .newTopic {
    background-color: white;
    .zuixin {
      color: #1dacf9;
      border-bottom: 3rem solid #1dacf9;
      line-height: 40rem;
      padding: 0 15rem;
      font-size: 17rem;
      width: 98rem;
    }
    ul.topics {
      li {
        padding: 10rem 15rem;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        height: 46rem;
        border-bottom: 1rem solid #e9e9e9;
        span {
          width: 20rem;
          height: 20rem;
          border: 1rem solid #ee4653;
          color: #ee4653;
          border-radius: 3rem;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-right: 10rem;
        }
        h5 {
          line-height: 25rem;
          color: rgb(51, 51, 51);
          font-size: 18rem;
        }
      }
    }
  }
}
</style>
